body {
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  color: #333;
}

p {
  color: #666;
}

#loginPage {
  text-align: center;
}

#dashboard {
  flex-direction: column;
  height: 100vh;
  display: flex;
}

header {
  color: #fff;
  background-color: #007bff;
  justify-content: space-between;
